sion will be an asset in making optimal decisions in the game. ")]),_c('p',[_vm._v(" There were a few years or months here and there that I took breaks from table tennis. It was during such a time in late 2015 when I learned of a tournament in University of Toronto. I met challenging and tough players but I was able to win the tournament of pool B. It was nice of the organizers to reward me with a UofT sports T-shirt and a certificate. ")]),_c('p',[_vm._v(" Throughout my life, especially recently, I have noticed that playing table tennis helps keep me healthy and fit. I believe this sport is a great combination of physical and mental fitness. ")])])}]

